<template>
  <div id="me">
    <header>
      <div class="statusbar"></div>
      <div class="head">
        <img src="../../assets/me/list@3x.png" alt="" @click="showSide">
        <ul>
          <li @click="tabActive(1)">
            Device
            <div class="line" v-if="active === 1"></div>
          </li>
          <li @click="tabActive(2)">
            Room
            <div class="line" v-if="active === 2"></div>
          </li>
        </ul>
      </div>
    </header>
    <div class="content" v-if="active === 1">
      <div class="full" v-if="deviceformat.length === 0">
        <img src="../../assets/me/logo@3x.png" alt="">
        <h3>Wellcome to growroomate</h3>
        <p>Please add a roomate smart device clicking below</p>
      </div>
      <div class="list" v-if="deviceformat.length > 0">
        <ul>
          <li class="list_li" v-for=" item in deviceformat" :key="item.id"
            @touchstart="chooseItem($event, 'start', item)"
            @touchmove="chooseItem($event, 'move', item)"
            @touchend="chooseItem($event, 'end', item)"
            :style="{backgroundColor:item.battery <= 0.2 ? '#fc403f':''}">
            <div class="left">
              <img :src="item.icon" alt="">
              <span>{{item.name}}</span>
            </div>
            <div class="right">
              <span>{{item.status}}</span>
              <img src="../../assets/me/next@3x.png" alt="">
            </div>
            <div class="side">
              <div class="rename" @click="rename(item)">
                Rename
              </div>
              <div class="delate" @click="remove(item)">
                <span>Delete</span>
              </div>
            </div>
          </li>
        </ul>
        <span class="options">Press on your device to view</span>
      </div>
      <div class="button" @click="addDevice(1)" ref="button">
        Add Devices
      </div>
    </div>
    <!-- ---------------------------------------------------active ---2 ---------------------------------------->
    <div class="content_2" v-show="active === 2">
      <div class="full" v-if="roomList.length === 0">
        <img src="../../assets/me/logo@3x.png" alt="">
        <h3>Group devices into rooms for easier managment
    of multi-room control</h3>
        <p>Please add a room by clicking below</p>
      </div>
      <div class="list" v-if="roomList.length > 0">
        <ul>
          <li class="list_li" v-for="item in roomListFormat" :key="item.id"
            @touchstart="chooseItem($event, 'start')"
            @touchmove="chooseItem($event, 'move')"
            @touchend="chooseItem($event, 'end', item)">
            <div class="left">
              <span>{{item.name}}</span>
            </div>
            <div class="right" >
              <img alt=""
                :src="icon"
                v-for="icon in item.icon"
                :key="icon.id"
                v-if="item.icon.length > 0"
              >
            </div>
            <div class="side">
              <div class="rename" @click="rename(item)">
                <span>Rename</span>
              </div>
              <div class="delate" @click="remove(item)">
                <span>Delete</span>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="button" @click="addDevice(2)">
        Add Rooms
      </div>
    </div>
    <side-bar></side-bar>
    <!-- Updates -->
    <messageBox setHeight="190" setType="confirm" v-show="messageBoxType === 'showUpdate'" sideMessage="true">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span>new version update</span>
        </div>
        <div class="alert_describe">The content of new version update</div>
        <div class="alert_operation">
          <div class="determine_one" @click="alertChoose(0)" style="background:#cccccc;">Cancel</div>
          <div class="determine_one" @click="alertChoose(1)" style="background:#13af68;">OK</div>
        </div>
      </div>
    </messageBox>
    <!-- logout -->
    <messageBox setHeight="190" setType="confirm" v-show="messageBoxType === 'showLogout'" sideMessage="true">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span>Logout</span>
        </div>
        <div class="alert_describe">Are you sure to logout</div>
        <div class="alert_operation">
          <div class="determine_one" @click="alertChoose(2)" style="background:#cccccc;">Cancel</div>
          <div class="determine_one" @click="alertChoose(3)" style="background:#13af68;">Yes</div>
        </div>
      </div>
    </messageBox>
    <!-- Rename -->
    <messageBox setHeight="235" setType="confirm" marginTop="-60%" v-show="messageBoxType === 'Rename'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span>Rename</span>
        </div>
        <div class="rename">
          <input type="text" v-model="renameVal" @input="nameExp()" maxlength="16" ref="EXPVAL">
          <img src="../../assets/me/del@3x.png" alt="" @click="clearVal" v-show="renameVal !==''">
        </div>
        <div class="describe" :style="{color:renameExp? '': 'red'}">Name must between 2-16 characters long, only letters and numbers allowed</div>
        <div class="alert_operation">
          <div class="determine_two" @click="alertChoose(0)" style="background:#cccccc;">Cancel</div>
          <div class="determine_two" @click="alertChoose(4)" :style="{background:renameVal ? '#13af68': '#cccccc'}">Yes</div>
        </div>
      </div>
    </messageBox>
    <!-- Remove -->
    <messageBox setHeight="190" setType="confirm" marginTop="-60%" v-show="messageBoxType === 'Remove'">
      <div slot="confirmColumn" class="common_alert">
        <div class="alert_icon">
          <span>{{removeType}}</span>
        </div>
        <div class="alert_describe">Are you sure to remove it?</div>
        <div class="alert_operation">
          <div class="determine_one" @click="alertChoose(0)" style="background:#cccccc;">Cancel</div>
          <div class="determine_one" @click="alertChoose(7)" style="background:#13af68;">Yes</div>
        </div>
      </div>
    </messageBox>
    <!-- toast -->
    <div class="messageToast" v-if="animFlag || removeSuccess || removeFailed" >
      <div v-if="animFlag">
        <img src="../../assets/device/loading@3x.png" alt="" :class="animFlag? 'Rotation': ''">
        <span>{{messageToastType}}</span>
      </div>
      <div v-if="removeSuccess" >
        <img src="../../assets/device/succeed-icon@3x.png" alt="">
        <span v-if="active === 1">Remove the device successfully</span>
        <span v-if="active === 2">Remove the room successfully</span>

      </div>
      <div v-if="removeFailed">
        <img src="../../assets/device/wrong@3x.png" alt="">
        <span style="marginTop:10px" v-if="active === 1">Failed to remove the device,please retry</span>
        <span style="marginTop:10px" v-if="active === 2">Failed to remove the room,please retry</span>

      </div>
    </div>
    <transition mode="in-out" name="slide-left">
        <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import SideBar from '../../components/common/sideBar.vue'
import headTop from '../../components/common/head'
import messageBox from '../../components/common/messageBox'

import {
  reNameRequest,
  versionUpdateRequest,
  logoutUserRequest,
  // removeRequest,
  // queryRoomsRequest,
  deviceListRequest,
  unbundlingDeviceRequest,
  controlDeviceRequest,
  relieveControlDeviceRequest,
  setStatusBarRequest,
  commonPatternListDataRequest,
  temperatureAndHumidityDataRequest,
  timingAndTemperatureDataRequest
} from '../../api/method'
import {
  deviceListResponse,
  reNameResponse,
  networkStatusResponse,
  unbundlingDeviceResponse,
  logoutUserResponse,
  userInformationResponse,
  controlDeviceResponse,
  removeResponse,
  commonPatternListDataResponse,
  temperatureAndHumidityDataResponse,
  powerSwitchResponse,
  deviceHistoryReportDataResponse,
  timingAndTemperatureDataResponse,
  wifiPowerSwitchResponse
  // queryRoomsResponse
  // versionUpdateResponse
} from '../../api/getData'
import { getStore, setStore } from '../../utils'

const heartIMG = require('../../assets/device/heater@3x.png')
const humideyIMG = require('../../assets/device/humidier@3x.png')
const lightIMG = require('../../assets/device/light@3x.png')
export default {
  components: {
    'side-bar': SideBar,
    'head-top': headTop,
    messageBox
  },
  data () {
    return {
      active: 1, // 当前菜单
      flagdelter: true, // 左滑
      startX: null, // 水平移动距离
      startY: null, // 垂直移动距离
      delterflag: false, // true 左滑按钮显示
      endX: null,
      Rename: false, // 重命名弹窗
      renameVal: this.$store.state.user.deviceName,
      renameExp: true,
      Remove: false,
      animFlag: false, // 动画时间5s
      removeSuccess: false, // 移除成功
      removeFailed: false, // 移除失败
      removeType: null, // messageBox remove type device rooms
      messageToastType: null, // messageToast remove type device rooms
      liNode: null, // 当前页面的li
      deviceData: [], // device list
      currentRoomFlag: null,
      deviceTest: [],
      devicesInit: {
        mac: '',
        name: '',
        status: '', // right text
        mode: '',
        timingId: null,
        temperature: 0,
        humidity: 0,
        run: false, // 结束
        temperTask: {},
        humidTask: {},
        timingTask: {}
      },
      timer: null // 解绑超时
    }
  },
  computed: {
    ...mapGetters(['messageType', 'getcurrentMac', 'getRoomdata', 'getDevicelist', 'getDevicestatus', 'getDevicestatusInit', 'getDevicesdata', 'currentRouter', 'mqttState']),
    showLogout () {
      return this.$store.getters.showLogout
    },
    showUpdate () {
      return this.$store.getters.showUpdate
    },
    messageBoxType () {
      return this.messageType
    },
    currentDeviceMAC () {
      return this.getcurrentMac
    },
    deviceList () {
      return this.getDevicelist // 设备列表
    },
    devicesData () {
      return this.getDevicesdata
    },
    deviceformat () {
      let obj = []
      obj = this.deviceList
      // let localTiming = JSON.parse(getStore('Timing'))
      // let localtempHumid = JSON.parse(getStore('tempHumid'))
      if (obj.length > 0) {
        obj.forEach(element => {
          if (this.deviceTest.length > 0) {
            this.deviceTest.forEach(ele => {
              if (element.mac === ele.mac) {
                element.name = ele.name
              }
            })
          }
          switch (element.mode) {
            case 'heartIMG':
              element.icon = heartIMG
              element.status = element.temperature + '℃'
              break
            case 'humideyIMG':
              element.icon = humideyIMG
              element.status = element.humidity + '%'
              break
            case 'lightIMG':
              element.icon = lightIMG
              element.status = element.run ? 'ON' : 'oFF'
              break
            default:
              element.icon = heartIMG
              element.mode = 'heartIMG'
              let deviceData = JSON.parse(getStore('devicesData'))
              console.log(deviceData)
              if (deviceData) {
                deviceData.forEach((item) => {
                  if (item.mac === element.mac) {
                    switch (item.mode) {
                      case 'heartIMG':
                        element.icon = heartIMG
                        element.mode = item.mode
                        this.$set(element, 'mode', item.mode)
                        element.status = item.temperature + '℃'
                        break
                      case 'humideyIMG':
                        element.icon = humideyIMG
                        element.mode = item.mode
                        element.status = item.humidity + '%'
                        break
                      case 'lightIMG':
                        element.icon = lightIMG
                        element.mode = item.mode
                        element.status = item.run ? 'ON' : 'OFF'
                        break
                      default:
                        // element.mode = 'heartIMG'
                        break
                    }
                  }
                })
              }
              break
          }
          // element.battery = 1
          // element.status = deviceMode.state? 'ON':'OFF'
        })
      }
      console.log(obj)
      this.setDevicelist(obj)
      return obj
    },
    roomList () {
      return this.getRoomdata
    },
    roomListFormat () {
      let roomList = this.roomList
      console.log(roomList)
      roomList.forEach(element => {
        let arr = []
        console.log(element.icon)
        element.icon.forEach((item, index) => {
          switch (item) {
            case 'heartIMG':
              arr[index] = heartIMG
              break
            case 'humideyIMG':
              arr[index] = humideyIMG
              break
            case 'lightIMG':
              arr[index] = lightIMG
              break
            default:
              break
          }
        })
        console.log(arr)
        element.icon = arr
      })
      console.log(roomList)
      return roomList
    }
  },
  created () {
    // 获取设备列表返回
    deviceListRequest(this.NAVIGATOR, this)
    deviceListResponse(this.NAVIGATOR, this, 'Me')
    setStatusBarRequest(this.NAVIGATOR, this, false, '')
    // 网络转态返回
    networkStatusResponse(this.NAVIGATOR, this)
    reNameResponse(this.NAVIGATOR, this)
    // 插座控制继电器开关返回
    wifiPowerSwitchResponse(this.NAVIGATOR, this)
    // 版本gengxin
    // versionUpdateResponse(this.NAVIGATOR, this)
    // 退出登录
    logoutUserResponse(this.NAVIGATOR, this)
    // 查询user信息
    userInformationResponse(this.NAVIGATOR, this)

    // 控制设备返回
    controlDeviceResponse(this.NAVIGATOR, this)
    // 定时数据返回
    commonPatternListDataResponse(this.NAVIGATOR, this, 'Me')
    if (this.$root.ISDEBUG) {
      // window.queryRoomsResponse('[{"name":"roomName","devices":"00:00:01:00:00&00:00:02:00:00","icon":""}]')
      window.deviceListResponse('[{"name":"Hearter","switch":false,"icon":"heartIMG","status":"25%","mac":"00:00:01:00:00","encrypted":true,"isBinding":true,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}},{"name":"","switch":false,"icon":"heartIMG","status":"50%","mac":"00:00:02:00:00","encrypted":true,"isBinding":true,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}}]')
      // window.userInformationResponse(true, '{"headPic":"http:\/\/file.startai.com.cn:3108\/group1\/M00\/00\/0F\/Zy1orFubinyEPP5tAAAAAAAAAAA661.jpg","email":"1451560884@qq.com","isHavePwd":true,"firstName":"tom","lastName":"Curise"}')
      setTimeout(() => {
        window.deviceListResponse('[{"name":"Humidity","switch":false,"icon":"heartIMG","status":"50%","mac":"00:00:02:00:00","encrypted":true,"isBinding":true,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}}]')
      }, 3000)
      window.deviceListResponse('[{"name":"jjkkk","switch":false,"icon":"heartIMG","status":"25%","mac":"00:00:01:00:00","encrypted":true,"isBinding":true,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}}]')
      window.deviceListResponse('[{"name":"jjkkk","switch":false,"icon":"heartIMG","status":"25%","mac":"00:00:04:00:00","encrypted":true,"isBinding":true,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}}]')
      window.deviceListResponse('[{"switch":false,"alias":"","mac":"84:0D:8E:88:B0:17","id":"91E5AB6B8561FC54","connstatus":1,"bindingtime":1545132138887,"type":1,"wifiInfomation":{"strength":-69,"ssid":"STARTAI"},"state":1,"name":"Irrigationpump"},{"switch":true,"alias":"Humidifier","mac":"84:0D:8E:88:B0:4A","id":"C351CD73F02BAFB2","connstatus":1,"bindingtime":1545029932201,"type":1,"state":1,"name":"Humidifier"}]')
    }
  },
  mounted () {
    this.$nextTick(() => {
      setTimeout(() => {
        this.$refs.button.className = 'button_opacity button'
      }, 350)
    })
    temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'Me')
    powerSwitchResponse(this.NAVIGATOR, this)
    // 定时温度数据
    timingAndTemperatureDataResponse(this.NAVIGATOR, this, 'Me')
    deviceHistoryReportDataResponse(this.NAVIGATOR, this)
    this.deviceList.forEach(element => {
      setTimeout(() => {
        // 温湿度数据
        temperatureAndHumidityDataRequest(this.NAVIGATOR, this, element.mac)
        clearTimeout()
      }, 300)
      setTimeout(() => {
        // 定时温度数据请求
        timingAndTemperatureDataRequest(this.NAVIGATOR, this, element.mac, 2)
        clearTimeout()
      }, 600)
      setTimeout(() => {
        // 定时温度数据请求
        timingAndTemperatureDataRequest(this.NAVIGATOR, this, element.mac, 1)
        clearTimeout()
      }, 900)
      setTimeout(() => {
        // 定时数据
        commonPatternListDataRequest(this.NAVIGATOR, this, this.currentDeviceMAC, 2)
        clearTimeout()
      }, 1200)
    })
    if (JSON.parse(getStore('roomData'))) {
      this.setRoomdata(JSON.parse(getStore('roomData')))
    }
    // remove 设备或房间回复
    unbundlingDeviceResponse(this.NAVIGATOR, this)
    removeResponse(this.NAVIGATOR, this)
    this.deviceList_init() // 设备列表数据初始化
    if (this.$root.ISDEBUG) {
      // window.commonPatternListDataResponse('00:00:01:00:00', '[]', '[{"time":"00:00","time2":"00:00","id":1,"state":false,"week":5}]')
      // window.commonPatternListDataResponse('00:00:03:00:00', '[]', '[{"time":"00:00","time2":"00:00","id":1,"state":false,"week":5}]')
      window.commonPatternListDataResponse('00:00:03:00:00', '[]', '[{"time":"10:57","time2":"24:0","id":1,"state":true,"week":0,"onCycle":"5:0","offCycle":"0:1"},{"time":"15:00","time2":"16:00","id":2,"state":false,"week":0,"onCycle":"0:0","offCycle":"0:0"},{"time":"1:1","time2":"1:2","id":3,"state":false,"week":0,"onCycle":"3:3","offCycle":"5:5"}]')

      window.temperatureAndHumidityDataResponse('00:00:02:00:00', '{"temperature":{"currentValue":0,"hotAlarmValue":20,"hotAlarmSwitch":false,"codeAlarmValue":20,"codeAlarmSwitch":false},"humidity":{"currentValue":20,"hotAlarmValue":50,"codeAlarmValue":0,"hotAlarmSwitch":true,"codeAlarmSwitch":false}}')
      window.temperatureAndHumidityDataResponse('00:00:03:00:00', '{"temperature":{"currentValue":0,"hotAlarmValue":20,"hotAlarmSwitch":false,"codeAlarmValue":20,"codeAlarmSwitch":false},"humidity":{"currentValue":20,"hotAlarmValue":50,"codeAlarmValue":0,"hotAlarmSwitch":false,"codeAlarmSwitch":false}}')
      window.timingAndTemperatureDataResponse('00:00:01:00:00', '[{"time": "21:25","time2": "23:25","id": 1,"state": true,"week": 5,"onCycle": "01:01","offCycle": "00:01","alarmValue": 25,"currentValue": 20,"mode": 1}]')
    }
  },
  methods: {
    ...mapActions(['showMessagesBox', 'roomName', 'addevicePath', 'setCurrentMac', 'surName', 'userName', 'setHeadpic', 'setRoomdata', 'setDevicelist', 'setDevicesdata']),
    showSide () {
      this.$store.dispatch('showSideBar')
      this.$store.dispatch('hideSideBarmask')
    },
    deviceList_init () {
    },
    tabActive (num) {
      console.log(this.delterflag, num)
      if (!this.delterflag) {
        switch (num) {
          case 1:
            this.active = 1
            break
          case 2:
            this.active = 2
            break
          default:
            break
        }
      } else {
        this.hideDelet()
      }
    },
    alertChoose (num) {
      switch (num) {
        case 0: // 检查更新
          this.showMessagesBox({type: null})
          break
        case 1:
          this.showMessagesBox({type: null})
          versionUpdateRequest(this.NAVIGATOR, this)
          break
        case 2: // 退出
          this.showMessagesBox({type: null})
          break
        case 3:
          this.showMessagesBox({type: null})
          // this.$store.dispatch('hideSideBarmask')
          // this.$store.dispatch('hideSideBar')
          logoutUserRequest(this.NAVIGATOR, this)
          if (this.$root.ISDEBUG) {
            window.logoutUserResponse(true)
          }
          break
        case 4: // rename ok
          if (this.renameVal !== '' && this.renameExp) {
            if (this.active === 1) {
              for (let i in this.deviceformat) {
                if (this.renameVal === this.deviceformat[i].name) {
                  this.$store.state.ToastType = 'sameRoom'
                  this.hideDelet()
                  // this.showMessagesBox({type: null})
                  return false
                }
              }
              reNameRequest(this.NAVIGATOR, this, this.currentDeviceMAC, this.renameVal)
              this.$store.state.user.deviceName = this.renameVal
              if (this.$root.ISDEBUG) {
                window.reNameResponse(this.currentDeviceMAC, true)
              }
            } else {
              let roomData = JSON.parse(getStore('roomData'))
              // roomData.splice(item.id, 1)
              for (let i in roomData) {
                if (this.renameVal === roomData[i].name) {
                  this.$store.state.ToastType = 'sameRoom'
                  this.hideDelet()
                  // this.showMessagesBox({type: null})
                  return false
                }
              }
              roomData.forEach((element) => {
                if (element.id === this.currentRoomFlag) {
                  element.name = this.renameVal
                }
              })
              setStore('roomData', roomData)
              this.setRoomdata(roomData)
            }
            this.showMessagesBox({type: null})
            this.hideDelet()
          }
          break
        case 5:
          this.Rename = false
          break
        case 6: // 退出
          this.Remove = false
          break
        case 7:
          if (this.active === 1) {
            // removeRequest(this.NAVIGATOR, this, 1, this.currentDeviceMAC)
            unbundlingDeviceRequest(this.NAVIGATOR, this, this.currentDeviceMAC)
            console.log(this.currentDeviceMAC)
            this.animFlag = true
            this.renameVal = 'Rename'
            if (this.$root.ISDEBUG) {
              setTimeout(() => {
                window.unbundlingDeviceResponse(true, this.currentDeviceMAC)
                clearTimeout()
              }, 3000)
            }
            this.timer = setTimeout(() => {
              this.animFlag = false
              this.removeFailed = true
              clearTimeout()
              setTimeout(() => {
                this.removeFailed = false
                clearTimeout()
              }, 2000)
            }, 6000)
          } else if (this.active === 2) {
            // removeRequest(this.NAVIGATOR, this, 2, this.currentRoomFlag)
            console.log(this.currentRoomFlag)
            this.animFlag = false
            if (this.$root.ISDEBUG) {
              setTimeout(() => {
                // window.removeResponse(false)
                clearTimeout()
              }, 5000)
            }
            let roomData = JSON.parse(getStore('roomData'))
            let macArray = roomData[this.currentRoomFlag].mac
            let deviceList = this.deviceList
            let devicesData = this.devicesData
            if (macArray && devicesData) { // 空房间
              macArray = macArray.split('&')
              macArray.forEach(element => {
                devicesData.forEach(item => {
                  if (item.mac === element) {
                    deviceList.push(item)
                  }
                })
              })
              this.setDevicelist(deviceList)
            }
            roomData.splice(this.currentRoomFlag, 1)
            roomData.forEach((element) => {
              if (element.id > this.currentRoomFlag) {
                element.id = element.id - 1
              }
            })
            setStore('roomData', roomData)
            this.setRoomdata(roomData)
          }
          this.showMessagesBox({type: null})
          this.hideDelet()
          break
        default:
          break
      }
    },
    addDevice (active) {
      console.log(active)
      if (active === 1) {
        this.addevicePath('/Me')
        this.$router.push({path: '/Me/addevice'})
      } else if (active === 2) {
        this.$router.push({path: '/Me/addRoom'})
      }
    },
    // 左滑
    chooseItem (e, type, item) {
      let Listli = e.target
      while (Listli.className !== 'list_li') {
        Listli = Listli.parentNode
        if (Listli.className === 'side') {
          return false
        }
      }
      let Slistli = document.getElementsByClassName('list_li')
      this.liNode = Slistli
      switch (type) {
        case 'start':
          this.hideDelet()
          // this.isMove = false
          this.startX = e.changedTouches['0'].pageX
          this.startY = e.changedTouches['0'].pageY
          break
        case 'move':
        //   this.isMove = true
          let liLeft = e.changedTouches['0'].pageX - this.startX
          let moveY = e.changedTouches['0'].pageY - this.startY
          console.log(Math.abs(moveY))
          if (liLeft < -35 && this.flagdelter && Math.abs(moveY) < 40) {
            if (liLeft < -120) {
              this.delterflag = true
              Listli.style.marginLeft = -120 + 'px'
            } else if (liLeft > 0) {
              Listli.style.marginLeft = 0 + 'px'
            } else {
              if (liLeft === -120) {
                console.log(2)
                this.delterflag = true
              }
              Listli.style.marginLeft = liLeft + 'px'
            }
          } else {
            this.hideDelet()
          }
          // }
          // console.log(e.changedTouches["0"].pageX)
          break
        case 'end':
          liLeft = e.changedTouches['0'].pageX - this.startX
          moveY = e.changedTouches['0'].pageY - this.startY
          if (Math.abs(moveY) < 25) {
            this.endX = e.changedTouches['0'].pageX
            if ((this.startX - this.endX) < 120 && (this.startX - this.endX) > 0) {
              let x = this.endX - this.startX
              let timer = setInterval(() => {
                if (x > -120) {
                  x += -10
                } else {
                  x = -120
                  this.delterflag = true // 删除显示
                  clearInterval(timer)
                }
                Listli.style.marginLeft = x + 'px'
              }, 30)
            }
          }
          console.log(Math.abs(this.endX - this.startX))
          if (Math.abs(this.endX - this.startX) === 0 && !this.delterflag) {
            if (this.active === 1) {
              console.log(JSON.stringify(item))
              controlDeviceRequest(this.NAVIGATOR, this, item)
              console.log(item)
              this.setCurrentMac(item.mac) // 操作设备标识
              this.$router.push({'path': '/temperature', query: {'device': item.name, 'MAC': this.currentDeviceMAC}})
              console.log(this.currentDeviceMAC)
            } else if (this.active === 2) {
              this.$router.push({'name': 'rooms', 'params': {'device': item.name, 'id': item.id}})
              this.roomName(item.name)
            }
          }
          // 判断左滑还是点击
          // Slistli = document.getElementsByClassName('list_li')
          break
      }
    },
    rename (item) {
      if (this.active === 1) {
        this.renameVal = item.name
        this.setCurrentMac(item.mac)
        console.log(this.currentDeviceMAC)
      } else {
        this.currentRoomFlag = item.id
        this.renameVal = item.name
        console.log(this.currentRoomFlag)
      }
      this.showMessagesBox({type: 'Rename'})
    },
    nameExp () {
      // 输入内容格式验证
      let val = this.$refs.EXPVAL.value.trim()
      if (/^\w+$/.test(val)) {
        this.renameExp = true
      } else {
        this.renameExp = false
      }
    },
    clearVal () {
      this.renameVal = ''
    },
    remove (item) {
      if (this.active === 1) {
        this.removeType = 'Remove Devices' // 提示语
        this.messageToastType = 'Removing the device'
        this.setCurrentMac(item.mac) // 删除设备标识
      } else {
        this.messageToastType = 'Removing the room'
        this.removeType = 'Remove Rooms'
        this.currentRoomFlag = item.id // 删除room标识
      }
      this.showMessagesBox({type: 'Remove'})
    },
    hideDelet () {
      let Slistli = document.getElementsByClassName('list_li')
      for (let i = 0; i < Slistli.length; i++) {
        if (parseInt(Slistli[i].style.marginLeft) < 0) {
          this.flagdelter = false
          let x = parseInt(Slistli[i].style.marginLeft)
          let timer = setInterval(() => {
            if (x < 0) {
              x += 10
            } else {
              x = 0
              this.flagdelter = true
              this.delterflag = false
              clearInterval(timer)
            }
            if (Slistli[i]) {
              Slistli[i].style.marginLeft = x + 'px'
            }
          }, 30)
        }
      }
    }
  },
  watch: {
    // deviceList (newval) {
    //   newval.forEach(element => {
    //     setTimeout(() => {
    //       // 温湿度数据
    //       temperatureAndHumidityDataRequest(this.NAVIGATOR, this, element.mac)
    //       clearTimeout()
    //     }, 300)
    //     setTimeout(() => {
    //       // 定时温度数据请求
    //       timingAndTemperatureDataRequest(this.NAVIGATOR, this, element.mac, 2)
    //       clearTimeout()
    //     }, 600)
    //     setTimeout(() => {
    //       // 定时温度数据请求
    //       timingAndTemperatureDataRequest(this.NAVIGATOR, this, element.mac, 1)
    //       clearTimeout()
    //     }, 900)
    //     setTimeout(() => {
    //       // 定时数据
    //       commonPatternListDataRequest(this.NAVIGATOR, this, this.currentDeviceMAC)
    //       clearTimeout()
    //     }, 1200)
    //   })
    // }
  },
  destroyed () {
    relieveControlDeviceRequest(this.NAVIGATOR, this)
  }
}
</script>

<style scoped lang="less">
