              padding-top: 27px;
            }
            .rename{
              width: 50%;
              background: url(../../assets/device/Rename@3x.png);
              background-size: cover;
              position: relative;
              padding-top: 27px;
            }
            // span{
            //   font-size: 15px;
            //   color: #fcfcfc;
            //   // display: inline-block;
            //   vertical-align: baseline
            // }
          }
        }
        li:last-child {
          border-bottom: 1px solid #dadada;
        }
      }
      .options{
        font-size: 26px;
